	import Head from "../public/head.vue"
  import Bus from '../assets/eventBus'
	export default {
		data() {
			return {
				activeIndex:'1',
				isBasic:false,
        img:'./static/img/home/4.jpg',
				hotList:[],
        search:''
			};
		},
		components:{
			Head,
		},
		methods:{
		  sosuo(){
		    this.isBasic = !this.isBasic
      },
			handleSelect(key){
				this.activeIndex=key;
				if(key=='1'){
					this.$router.push('/wonderful')
				}else if(key=='2'){
					this.$router.push('/outdoor')
				}else if(key=='3'){
					this.$router.push('/ethnic')
				}else if(key=='4'){
					this.$router.push('/local')
				}
			},
      toSearch(){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: this.search,
            type:1
          }
        })
      },
      searchStory(item,index){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: item.city,
            type:1
          }
        })
      }
		},
    mounted() {
		  window.scroll(0,0)
      Bus.$on('hotPlace',(msg)=>{
        this.hotList = msg
      })
    },
    destroyed(){

    }
  }



